.cardapio {
    background: $c3;
    padding: 0 0 120px;

    .burger-wrp {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;

        figure {
            position: relative;
            width: 33%;
            max-width: 370px;

            svg {
                z-index: 1;
                position: absolute;
                right: 0;
            }

            img {
                z-index: 2;
            }

            figcaption {
                position: absolute;
                z-index: 3;
                top: 8px;
                left: 9px;
                color: $cWhite;
                padding: 15px 8px 12px 18px;
                text-align: center;
                width: 294px;
                height: 284px;
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                align-items: flex-start;

                h3 {
                    font-size: 2.319rem;
                    font-family: $titleFont;
                    text-shadow: 1px 2px 0px rgba(0, 0, 0, 0.63);
                    line-height: .75;

                    span {
                        font-size: 0.686rem;
                        display: block;
                        margin: 4px auto 0;
                    }
                }
                .ingredients {
                    font-size: 0.824rem;
                    font-weight: 400;
                    text-align: center;
                    font-family: $contentFont;
                    margin-bottom: 100px;
                    display: flex;
                    justify-content: center;
                    width: 100%;
                }
                .including {
                    display: flex;
                    align-self: flex-end;
                    margin-right: auto;
                    text-align: left;
                    font-size: 0.685rem;
                    text-align: left;
                    font-family: $titleFont;
                    text-shadow: 1px 2px 0px rgba(0, 0, 0, 0.63);
                    line-height: 1;
                }
                .price {
                    display: flex;
                    align-self: flex-end;
                    color: $c2;
                    font-size: 2.284rem;
                    font-weight: 900;
                    text-shadow: 2px 4px 12.6px rgba(0, 0, 0, 0.75);
                    font-family: $contentFont;
                    color: $cBlack;
                    -webkit-text-fill-color: $cBlack; 
                    -webkit-text-stroke-width: 5px;
                    -webkit-text-stroke-color: $cBlack;
                    position: relative;

                    &:before {
                        content: attr(data-price);
                        position: absolute;
                        top: 0;
                        left: 0;
                        font-size: 2.284rem;
                        color: $c2;
                        -webkit-text-fill-color: $c2; 
                        -webkit-text-stroke-width: 0px;
                        -webkit-text-stroke-color: $c2;
                        font-weight: 900;
                    }
                }

                &.veggie {
                    .ingredients {
                        margin: 113px auto 0;
                    }
                }
            }
        }
        p {
            text-align: center;
            color: $cWhite;
            font-family: $contentFont;
            font-size: 1.492rem;
            width: 100%;
            margin: 0 auto 35px;
    
            strong {
                font-weight: 900;
            }
        }
    }
    .cardapio-wrp {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
    }
    .col {
        width: 33%;
        span {
            font-family: $contentFont;
            color: $cWhite;
            font-size: 1.12rem;
            font-weight: 400;
            line-height: .7;
        }
        h3 {
            color: $c2;
            font-family: $titleFont;
            font-size: 1.751rem;
            letter-spacing: .1rem;
            line-height: .9;
            margin: 0 auto 13px;
        }
        p {
            font-size: 1.125rem;
            font-weight: 400;
            font-family: $contentFont;
            color: $cWhite;
            letter-spacing: .1rem;
            width: 85%;
        }
        
        .row {
            margin: 0 0 8px;

            &.row-0 {
                margin: 0 0 11px;
            }
            &.row-3 {
                margin: 25px 0;
                span {
                    line-height: 1.1;
                }
            }
            &.row-margin {
                margin: 0 0 45px;
            }
        }
    }
    .sobremesa {
        font-size: 1.354rem;
        font-weight: 700;
        color: $c2;
        font-family: $contentFont;
        text-align: center;
    }
}
#adicionais-icon, #adicionais-icon-2 {
    g {
        fill: transparent !important;
        stroke: $c2;
    }
}


